.header {
  background-image: url(/assets/images/Header-picture.svg);
  width: 100%;
  height: 70px;
  box-shadow: 0 4px 4px rgba(0, 0, 0, 0.25);
}

.headerWrapper {
  display: flex;
  justify-content: space-between;
  margin: 0 auto;
  max-width: 1440px;
}

.messengerPicture {
  height: 13px;
  margin: 12px 27px 0 0;
  cursor: pointer;
}

.logoPicture {
  margin: 12px 0 0 64px;
}

.textChatIcon {
  margin-right: 10px;
  color: whitesmoke;
  text-transform: uppercase;
  transition: .3s;
}

.btnChatWrapper {
  cursor: pointer;
}
